@import url("./components/Header/header.scss");
@import url("./components/Swipeable/swipeable.scss");
@import url("./components/Cart/cart.scss");
@import url("./components/Menu/menu.scss");

body {
	padding: 0;
	margin: 0;
	background-color: #eee;
}

body,
* {
	box-sizing: border-box;
	font-family: Arial, Helvetica, sans-serif;
}

.react-swipeable-view-container {
	height: 100% !important;

	& > div {
		height: 100%;
	}
}

.App {
	max-width: 500px;
	margin: 0 auto;
	position: relative;
}
